<template>
<div class="admin">

<h1 class="text-2xl"> Old Posts </h1>
<div class="heading">
      <h2 class="text-xl text-center mt-3">Select an old post to edit/delete it</h2>
    </div>
    <div class="edit">
      <div class="form">
        <input class="border-2 border-black rounded-full flex justify-self-center" v-model="findTitle" placeholder="Search">
        <div class="suggestions" v-if="suggestions.length > 0">
          <div class="suggestion" v-for="s in suggestions" :key="s.id" @click="selectItem(s)">{{s.title}}
          </div>
        </div>
      </div>
      <div class="upload" v-if="findItem">
        <img :src="findItem.path" />
      <br>
  <textarea class="border-2 border-gray-600 shadow-lg rounded-xl" id="" name="" rows="7" cols="50" v-model="findItem.title">  
  </textarea>

	<div class="actions" v-if="findItem">
        <button class="btn btn-primary m-2" @click="editItem(findItem)">Edit</button>
        <button class="btn btn-dark m-2" @click="deleteItem(findItem)">Delete</button>
	</div>
      </div>
    </div>

</div>
</template>

<style scoped>
.image h2 {
  font-style: italic;
  font-size: 1em;
}

.heading {
  display: flex;
  margin-bottom: 20px;
  margin-top: 20px;
}

.heading h2 {
  margin-top: 8px;
  margin-left: 10px;
}

